.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@import url(https://fonts.googleapis.com/css2?family=Monoton&family=Quicksand:wght@500;700&display=swap);* {
  box-sizing: border-box;
  margin: 0;
  padding: 0
}
html {
  font-size: 62.5%
}
body {
  font-family: sans-serif;
  font-family: Quicksand;
  color: #5a3e2b;
  font-size: 2.4rem;
  font-weight: 500;
  background-color: #5a3e2b;
  
}

.App {
  text-align: center;
  display: grid;
  grid-template-rows: auto auto 1fr auto;
  height: 100vh;
  width: 100%;
}

h1 {
  background-color: #f4a226;
  font-family: Monoton;
  font-size: 8rem;
  font-weight: 400;
  letter-spacing: -5px;
  padding: 2.4rem 0;
  text-align: center;
  text-transform: uppercase;
  word-spacing: 30px
}


.input-section {
  align-items: center;
    background-color: #e5771f;
    display: flex;
    gap: .8rem;
    justify-content: center;
    padding: 2.8rem 0
}
label{
  font-size: 2.4rem;
  margin-right: 1.6rem
}

input,
select,
button  {
  background-color: #ffebb3;
    border: none;
    border-radius: 10rem;
    color: #5a3e2b;
    cursor: pointer;
    font-family: inherit;
    font-size: 1.8rem;
    font-weight: 700;
    padding: 1.2rem 3.2rem
}

.input-section button {
  background-color: #76c7ad;
    text-transform: uppercase
}

.input-section button:hover {
  background-color: #008F8A;
}

.list-section {
  align-items: center;
  background-color: #5a3e2b;
  color: #ffebb3;
  display: flex;
  flex-direction: column;
  gap: 3.2rem;
  justify-content: space-between;
  padding: 4rem 0
}

.list-section ul {
  grid-gap: 1.2rem;
    align-content: start;
    display: grid;
    gap: 1.2rem;
    grid-template-columns: repeat(auto-fit,minmax(250px,1fr));
    justify-content: center;
    list-style: none;
    overflow: scroll;
    width: 80%
}

.list-section li {
  align-items: center;
    display: flex;
    gap: 1.2rem
}

.list-section li input[type="checkbox"] {
  accent-color: #e5771f;
    height: 2rem;
    width: 2rem
}


.btn-list{
  
  margin: 10px;
} 


.footer {
  background-color: #76c7ad;
  font-weight: 700;
  padding: 3.2rem 0;
  text-align: center
}

